import * as React from 'react';
import { useTable } from '@crafts/smart-grid';
import users from './users';

const generateData = (offset = 0) => users.splice(offset, offset + 100);

const api = (offset: number) =>
  new Promise<any>((res) => {
    setTimeout(() => {
      res(generateData(offset));
    }, 1200);
  });

interface IProps {
  rowHeight?: number;
  buffer?: number;
  limit?: number;
}

const Table = ({ rowHeight, buffer, limit }: IProps) => {
  const [data, setData] = React.useState(generateData());
  // const [loading, setLoading] = React.useState(false);
  const loading = React.useRef(false);
  const getData = React.useCallback(
    (sp: number) => {
      console.log(sp, loading.current);
      if (loading.current) {
        return;
      }
      console.log('CALL');
      loading.current = true;
      offset.current += 100;
      api(offset.current).then((newD) => {
        debugger;
        setData((d) => [...d, ...newD]);
        loading.current = false;
      });
    },
    [loading]
  );
  const { onScroll, tableRenderer } = useTable({
    totalCount: data.length + (loading.current ? 2 : 0),
    rowHeight: rowHeight || 39,
    buffer,
    limit,
    dynamicHeight: true,
    loadMore: getData,
  });
  const offset = React.useRef(0);

  console.log('TABLE');

  return (
    <div>
      <table style={{ overflow: 'hidden' }}>
        <thead>
          <tr>
            <th style={{ width: 200 }}>First Name</th>
            <th style={{ width: 200 }}>Last Name</th>
            <th style={{ width: 200 }}>Email</th>
          </tr>
        </thead>
        <tbody
          onScroll={onScroll}
          style={{ height: 200, overflow: 'auto', position: 'relative' }}
        >
          {tableRenderer(
            loading.current ? data.concat([null, null]) : data,
            (row, style, index, ref) =>
              row ? (
                <tr
                  ref={ref}
                  className="table-row"
                  data-testid={`table-row-${index}`}
                  style={style}
                  key={index}
                >
                  <td style={{ width: 200 }}>{row.firstName}</td>
                  <td style={{ width: 200 }}>{row.lastName}</td>
                  <td style={{ width: 200 }}>{row.email}</td>
                </tr>
              ) : (
                <tr
                  ref={ref}
                  className="table-row"
                  data-testid={`table-row-${index}`}
                  style={style}
                  key={index}
                >
                  <td>Loading</td>
                </tr>
              )
          )}
        </tbody>
      </table>
    </div>
  );
};

export default Table;
